/* Importing Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

/* Base Styles */
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  color: #121212;
  line-height: 1.5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

textarea, input, button {
  font: inherit;
  border: none;
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
  box-shadow: 0 0px 1px hsla(0, 0%, 0%, 0.2), 0 1px 2px hsla(0, 0%, 0%, 0.2);
  background-color: white;
}

button {
  color: #3992ff;
  font-weight: 500;
  cursor: pointer;
}

textarea:hover, input:hover, button:hover {
  box-shadow: 0 0px 1px hsla(0, 0%, 0%, 0.6), 0 1px 2px hsla(0, 0%, 0%, 0.2);
}

button:active {
  transform: translateY(1px);
  box-shadow: 0 0px 1px hsla(0, 0%, 0%, 0.4);
}

/* Utility Classes */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* Layout */
#root {
  display: flex;
  flex-direction: row;
  height: 100%;
}

#sidebar {
  width: 22rem;
  background-color: #f7f7f7;
  border-right: 1px solid #e3e3e3;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: fixed;
  padding: 1rem;
}

#sidebar h1 {
  font-size: 1rem;
  font-weight: 500;
  margin: 0;
  padding: 1rem 0;
  border-top: 1px solid #e3e3e3;
}

#sidebar nav {
  flex: 1;
  overflow-y: auto;
  padding-top: 1rem;
}

#sidebar nav a {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  border-radius: 8px;
  text-decoration: none;
  color: inherit;
  gap: 1rem;
}

#sidebar nav a:hover {
  background: #e3e3e3;
}

#sidebar nav a.active {
  background: hsl(224, 98%, 58%);
  color: white;
}

#detail {
  flex: 1;
  margin-left: 22rem;
  padding: 1.5rem;
}

/* Contact Form */
#contact {
  max-width: 40rem;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

#contact h1 {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
  line-height: 1.2;
}

#contact img {
  width: 100%;
  max-width: 12rem;
  height: auto;
  border-radius: 1.5rem;
  object-fit: cover;
}

/* Highlights */
#highlights {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: #f7f4e9;
  border-radius: 8px;
}

.box-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.box {
  padding: 15px 20px;
  border-radius: 8px;
  color: black;
  font-size: 1.5rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 150px;
}

/* Responsive Design */
@media (max-width: 768px) {
  #sidebar {
    position: relative;
    width: 100%;
    padding: 1rem;
  }

  #detail {
    margin-left: 0;
    padding: 1rem;
  }

  .box {
    font-size: 1.2rem;
    padding: 10px 15px;
  }
}

@media (max-width: 480px) {
  #contact h1 {
    font-size: 1.5rem;
  }

  .box {
    font-size: 1rem;
    padding: 10px;
  }

  .box-container {
    flex-direction: column;
  }
}
